.guess-number {
  @include flex($direction: column);
  padding-top: 36px;
}
.guess-number {
  color: var(--background-text-primary);
  margin-bottom: 20px;
  @include text(16px);
}

.inner-container {
  @include flex();
}

.input-container {
  @include flex(left, $align: left);
  border-radius: 25px;
  background-color: var(--input-background);
}

#number-input {
  padding: 10px;
  border: none;
  outline: none;
  border-radius: 25px 0 0 25px;
  background-color: var(--input-background);
  box-shadow: 3px 3px 0px 0px #00000040;
  max-width: 150px;
}

#number-input::placeholder {
  color: var(--input-placeholder);
  @include text();
}

.guess {
  padding: 12px;
  background-color: var(--background-text-primary);
  color: var(--secondary-text);
  border: none;
  border-radius: 0 25px 25px 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
  @include text();
  box-shadow: 3px 3px 0px 0px #00000040;
}

.message_numb {
  margin-top: 20px;
  text-align: center;
}

.message_numb.success {
  color: var(--green-text);
}

.message_numb.error {
  color: var(--red-text);
}
