  color: #1b1313;
}

.drop-menu__btn:hover {
  color: gray;
}

.drop-menu__img {
  transition: transform .3s linear;
}

.dropdown-menu {
  opacity: 0;
  border-right: 1px solid var(--background-text-primary);
  border-bottom: 1px solid var(--background-text-primary);
  border-left: 1px solid var(--background-text-primary);
  background-color: var(--secondary-text);
  text-align: center;
  width: 160px;
  pointer-events: none;
  z-index: 999;
  border-radius: 0 0 20px 20px;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  transition: opacity .2s;
  display: flex;
  position: absolute;
  top: 100%;
  left: -35px;
}

.dropdown-menu__item {
  height: 35px;
  cursor: pointer;
}

.dropdown-menu__item:hover {
  background-color: #f1f1f1;
}

.dark .dropdown-menu__item:hover .dropdown-menu__link {
  color: var(--secondary-text);
}

.dropdown-menu__item:nth-child(4) {
  border-radius: 0 0 20px 20px;
}

.dropdown-menu__link {
  color: var(--background-text-primary);
  text-align: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.dropdown-menu.is-open {
  opacity: 1;
  pointer-events: auto;
}

.drop-menu__img.rotated {
  transition: transform .3s linear;
  transform: rotate(180deg);
}

